import { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Event from './Event';
import Cookies from 'js-cookie';
import { handleError } from '../helpers';

const MyEvents = ({appState}) => {
    const [events, setEvents] = useState(null);

    const location = useLocation();
    
    useEffect(() => {
        const getHomeEvents = async () => {
            const response = await fetch(`/api_v1/events/mine`).catch(handleError);
            
            if (!response.ok) {
                throw new Error('Network response was not ok!');
            }
    
            const data = await response.json();
            setEvents(data);
        }

        getHomeEvents();
    }, [location.key])

    if (events === null) {
        return <div>Loading events...</div>
    }
    else if (events.length === 0) {
        return <div>You don't have any events. Create one!</div>
    }

    const deleteEvent = async (id) => {
        const options = {
            method: 'DELETE',
            headers: {
                'X-CSRFToken': Cookies.get('csrftoken'),
            }
        }

        const response = await fetch(`/api_v1/events/${id}/`, options).catch(handleError);

        if (!response.ok) {
            throw new Error('Network response was not ok!');
        }

        const index = events.findIndex(event => event.id === id);
        const newList = [...events];
        newList.splice(events[index], 1);
        console.log(newList);
        setEvents(newList);
    }

    const eventList = events.map((event, i) => 
        <Col key={i} sm={12} lg={6}>
            <Event 
                key={event.id} 
                appState={appState} 
                event={event} 
                deleteEvent={deleteEvent}/>
        </Col>
    )
    
    return (
        <main className="my-events-page">
            <Row className="gy-4">
                {eventList}
            </Row>
        </main>
    )
}

export default MyEvents;